.content-spotlight {
  background-color: #000;
  height: 48rem;
  margin-bottom: 6rem;
  position: relative;
  overflow: hidden;
}

.content-spotlight--slider {
  overflow: hidden;
}

.content-spotlight--main-content {
  width: 100%;
  transition: transform 1s;
  position: absolute;
  top: 0;
}

.content-spotlight--backdrop {
  object-fit: cover;
  object-position: top;
  width: 100%;
  height: 48rem;
}

.content-spotlight--text-container {
  margin-left: var(--padding-browser-side);
  border-radius: 5px;
  transition: opacity .3s;
  position: absolute;
  bottom: 13%;
  left: 0;
}

.content-spotlight--title {
  color: var(--c-white);
  font-size: var(--fs-large-l);
  text-transform: uppercase;
  text-shadow: 0 0 .6rem var(--c-black);
  font-weight: 600;
  line-height: 5rem;
}

.content-spotlight--more-container {
  color: var(--c-white);
  font-size: var(--fs-med-m);
