  text-decoration: underline;
}

.content-spotlight--more-icon {
  width: 1.6rem;
  height: 1.6rem;
}

.content-spotlight--overview {
  color: var(--c-white);
  padding: 1rem var(--padding-browser-side) 2rem var(--padding-browser-side);
  background-color: #000000bd;
  border-radius: 0 .6rem 0 0;
  max-width: 55rem;
  height: 24rem;
  transition: transform .27s ease-out;
  position: absolute;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  transform: translateX(-100%);
}

.content-spotlight--overview > div:first-child {
  align-items: center;
  gap: 1.2rem;
  display: flex;
}

.content-spotlight--overview::-webkit-scrollbar {
  width: .8rem;
}

.content-spotlight--overview::-webkit-scrollbar-thumb {
  background-color: var(--c-white);
  border: 1.2px solid #000000e6;
