  font-size: var(--fs-med-m);
  border-radius: .3rem;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 2rem;
  font-weight: 600;
  display: flex;
}

.content-spotlight--overview-back-btn {
  color: var(--c-white);
  background: none;
  border: none;
  width: 3.2rem;
  height: 3.2rem;
  transition: transform .25s;
  position: absolute;
  top: 10%;
  right: 4.8%;
}

.content-spotlight--overview-back-btn:hover {
  cursor: pointer;
  transform: scale(1.27);
}

.content-spotlight--overview-back-btn-icon {
  width: 2.8rem;
  height: 2.8rem;
}

.content-spotlight--overview-title-clip {
  text-overflow: clip clip;
  white-space: nowrap;
  position: relative;
  overflow: hidden;
}

.content-spotlight--overview-title {
  font-size: var(--fs-large-m);
