}

.content-spotlight--overview-description {
  font-size: var(--fs-med-m);
  width: 46.4rem;
}

.content-spotlight--btn {
  z-index: 50;
  background: none;
  border: none;
  transition: transform .25s;
  position: absolute;
}

.content-spotlight--btn:hover {
  cursor: pointer;
  transform: scale(1.27);
}

.content-spotlight--btn:focus {
  outline: none;
}

.content-spotlight--left-btn {
  bottom: 50%;
